@-webkit-keyframes move-in {
  0% {
    transform: translateX(100%);
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    color: #fff; }
  100% {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
    z-index: 1;
    color: #000; } }

@keyframes move-in {
  0% {
    transform: translateX(100%);
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    color: #fff; }
  100% {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
    z-index: 1;
    color: #000; } }

@-webkit-keyframes move-out {
  0% {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
    z-index: 1;
    color: #000; }
  80% {
    transform: translateX(-100%);
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    color: #fff; }
  100% {
    transform: translateX(-100%);
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    color: #fff;
    top: 79px;
    position: absolute; } }

@keyframes move-out {
  0% {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
    z-index: 1;
    color: #000; }
  80% {
    transform: translateX(-100%);
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    color: #fff; }
  100% {
    transform: translateX(-100%);
    opacity: 0;
    visibility: hidden;
    z-index: -1;
